class CastigatoriHandler {
    constructor() {
        if (jQuery(".castigatori").length === 0) {
            return;
        }

        this.init();
        this.handleDOM();
        this.handleEvents();
    }

    /**
     * Declare global variables
     */
    init() {}

    /**
     * Handle DOM queries
     */
    handleDOM() {}

    /**
     * Listen for events
     */
    handleEvents() {
        let self = this;
        
        // Custom scroll
        jQuery('.custom-scrollbar').mCustomScrollbar({
            axis: "y",
            scrollButtons: { enable: true },
            theme: "dark"
        });
        
        jQuery('.mCSB_draggerRail').appendTo('.mCSB_draggerContainer');
    }
}
