// init handlers
let layoutHandler = null;
let castigatoriHandler = null;
let registerPageHandler = null;
let quizHandler = null;
let homepageHandler = null;
let oreowalkHandler = null;

const initHandlers = () => {
    layoutHandler = new LayoutHandler();
    castigatoriHandler = new CastigatoriHandler();
    registerPageHandler = new RegisterPageHandler();
    quizHandler = new QuizHandler();
    homepageHandler = new HomepageHandler();
    oreowalkHandler = new OreowalkHandler();
};

// uncomment this to use jQuery
(($) => {
    $(document).ready(() => {
        initHandlers();
    });
})(jQuery);

