class OreowalkHandler {
    constructor() {
        if (jQuery(".oreo-slider").length === 0) {
            return;
        }

        this.init();
        this.handleDOM();
        this.handleEvents();
    }

    /**
     * Declare global variables
     */
    init() {}

    /**
     * Handle DOM queries
     */
    handleDOM() {
        this.oreoCarousel = jQuery(".oreo-carousel");
        this.videoWrapper = jQuery(".redirect-content .modal-btn");
        this.closeModal = jQuery(".close");
        this.videoModal = jQuery("#videoModal");
    }

    /**
     * Listen for events
     */
    handleEvents() {
        let self = this;

        this.videoWrapper.on("click tap", function (e) {
            e.preventDefault();

            let id = jQuery(this).attr("data-videoid");
            let videoUrl = "https://www.youtube.com/embed/" + id;
            self.videoModal.find("iframe").attr("src", videoUrl);

            setTimeout(function () {
                jQuery("#videoModal").modal("show");
            }, 100);
        });

        // Close video modal
        this.closeModal.on("click tap", function (e) {
            e.preventDefault();

            setTimeout(() => {
                self.videoModal.find("iframe").attr("src", "");
            }, 300);
        });

        this.videoModal.on('hidden.bs.modal', function (event) {
            setTimeout(() => {
                self.videoModal.find("iframe").attr("src", "");
            }, 300);
          })

        setTimeout(() => {
            self.initCarousel(self.oreoCarousel);
        }, 10);
    }

    initCarousel(carouselClass) {
        const swiper = new Swiper(carouselClass, {
            loop: false,
            slidesPerView: 5,
            allowTouchMove: false,
            parallax: true,
            autoplay: {
                delay: 2000,
                disableOnInteraction: false,
            },
            speed: 600,
            breakpoints: {
                // 1499: {
                //     loop: true,
                //     slidesPerView: 1,
                //     allowTouchMove: true,
                // },
                1199: {
                    loop: true,
                    slidesPerView: 1,
                    allowTouchMove: true,
                },
                767: {
                    loop: true,
                    slidesPerView: 1,
                    allowTouchMove: true,
                },
            },
            pagination: {
                el: ".swiper-pagination",
                type: "bullets",
                clickable: true,
            },
            navigation: {
                nextEl: ".swiper-button-next",
                prevEl: ".swiper-button-prev",
            },
        });
    }
}
