class QuizHandler {
    constructor() {
        if (jQuery(".quiz").length === 0) {
            return;
        }

        this.init();
        this.handleDOM();
        this.handleEvents();
    }

    /**
     * Declare global variables
     */
    init() {}

    /**
     * Handle DOM queries
     */
    handleDOM() {
        this.nextButton = document.querySelectorAll(".nextButton");
        this.finalButton = document.querySelector(".finalButton");
        this.allAnswersWrapper = document.querySelectorAll(".answers");
        this.allAnswers = document.querySelectorAll(".group");
        this.title = document.querySelector(".content h2");
        this.resultVariant = document.querySelector(".finalResult .image");
        this.shareFacebook = document.querySelector(".finalResult .share-answer a");
    }

    /**
     * Listen for events
     */
    handleEvents() {
        let self = this;
        let restultArray = [];
        let finalData = { 
            "shareTitle": 'Am aflat care biscuit OREO se potrivește stilului meu vestimentar!',
            "shareDescription": 'Un quiz simplu și sunt gata să-mi creez un #OOTD jucăuș inspirat de biscuitul meu OREO. În plus, oricine se înscrie în campania OREO #OOTD are șansa să câștige premii pentru outfituri inspirate: vouchere Fashion Days sau o sesiune de shopping cu un stilist profesionist în valoare de 7000 RON. Ție care Oreo ți se potrivește azi?',
            "shareImg": `` 
        };

        this.allAnswersWrapper.forEach((item) => {
            item.addEventListener("click", (e) => {
                const clicked = e.target.closest(".group");
                if (!clicked) return;

                self.allAnswers.forEach((answer) => answer.classList.remove("selected"));
                clicked.classList.add("selected");
            });
        });

        this.nextButton.forEach((item) => {
            item.addEventListener("click", (e) => {
                let dataQuestion = e.target.getAttribute("data-question");
                let currentQuestion = document.querySelector(`.question-${dataQuestion}`);
                let nextQuestion = document.querySelector(`.question-${Number(dataQuestion) + 1}`);

                self.allAnswers.forEach((answer) => {
                    if (answer.classList.contains("selected")) {
                        restultArray.push(document.querySelector(".selected").getAttribute("data-answer"));

                        currentQuestion.classList.remove("active");
                        nextQuestion.classList.add("active");

                        answer.classList.remove("selected");
                    }
                });
            });
        });

        this.finalButton.addEventListener("click", () => {
            self.allAnswers.forEach((answer) => {
                if (answer.classList.contains("selected")) {
                    restultArray.push(document.querySelector(".selected").getAttribute("data-answer"));

                    document.querySelector(".question-7").classList.remove("active");
                    document.querySelector(".finalResult").classList.add("active");
                    self.title.textContent = "Rezultat Quiz";

                    answer.classList.remove("selected");

                    const countAnswers = (restultArray) => {
                        return restultArray.reduce((result, value) => ({ ...result, [value]: (result[value] || 0) + 1 }), {});
                    };

                    const max = Object.keys(countAnswers(restultArray)).reduce((a, v) => Math.max(a, countAnswers(restultArray)[v]), -Infinity);
                    const result = Object.keys(countAnswers(restultArray)).find((v) => countAnswers(restultArray)[v] === max);

                    console.log(result);

                    self.resultVariant.setAttribute("src", `${absolutePath}/quiz/result-${result}.png`);

                    finalData.shareImg = `${location.origin}/share/quiz/share-result-${result}.png`;
                    self.shareFacebook.setAttribute("href", `https://www.facebook.com/sharer/sharer.php?u=${finalData.shareImg}`)
                }
            });
        });
    }
}
